<template>
  <div>
    <v-easy-camera @close="closeCamera()" :mustApprove="true" @approve="send"> </v-easy-camera>
  </div>
</template>

<script>
import EasyCamera from 'easy-vue-camera';

export default {
  components: {
    'v-easy-camera': EasyCamera,
  },
  methods: {
    closeCamera() {
      this.$router.push('/aufnahme');
    },
    send(data) {
      // Construct the <a> element
      const link = document.createElement('a');
      link.download = 'download.png';
      // Construct the uri
      console.log(data);
      const uri = 'data:image/png;base64,' + data;
      console.log(data);
      link.href = uri;
      document.body.appendChild(link);
      link.click();
      // Cleanup the DOM
      document.body.removeChild(link);
    },
  },
};
</script>

<style lang="scss" scoped></style>
